import React from 'react';
import { useAsync } from 'react-async';

import { functions } from '../utils/Connection';

import ComponentModalCodeSearch from './ComponentModalCodeSearch';
import ComponentSkeletonTracking from './ComponentSkeletonTracking';

const fetchPerson = async ({ tracking_code }, { signal }) => {

  return await functions().httpsCallable('returnTracking')([tracking_code])
    .then((data) => data.data)
    .then((data) => data[0]);
};

function ComponentAwaitModalCodeSearch({ assoc, useStage, setStage, useModal, setModal, useCode, setCode }) {

  // eslint-disable-next-line no-unused-vars
  const { receiver, date_approved, shipping_value, subtotal, tracking_code = '',
    // eslint-disable-next-line no-unused-vars
    street, number, complement, zone, district } = assoc[1];

  const { data, error } = useAsync({ promiseFn: fetchPerson, tracking_code });

  if (error) return error.message
  if (data) return <ComponentModalCodeSearch {...{ data, assoc, useStage, setStage, useModal, setModal, useCode, setCode }} />
  return <ComponentSkeletonTracking />
}

export default ComponentAwaitModalCodeSearch;
