import styled from 'styled-components';

import { BorderRadius, Checkbox } from './root';

export const Modify = styled.div`
    display: flex;

    ${({ fixed = false }) => fixed && `
        background-color: #e4e4e4;
        flex-direction: column;
        align-items: center;
    `};

    @media (max-width: 720px) {
        flex-direction: column;
        padding: 1.2rem 3rem;
        background: initial;
    }

    &:hover {
        background-color: #e4e4e4;
    }

    & > div:not(:last-child) {
        @media screen and (max-width: 720px) {
            margin-bottom: 1.2rem;
        }
    }

    & > div:first-child {
        max-width: 250px;
    }

    & > div:last-child {
        max-width: 440px;
    }

    & > div {
        width: 100%;
        padding: 10px;

        @media (max-width: 720px) {
            max-width: initial;
            padding: initial;
        }
    }
`

export const Title = styled.div`
  @media screen and (max-width: 720px) {
    font-weight: 700;
    color: rgba(0, 0, 0, 0.87);
  }
`

export const Status = styled.div`
  ${Checkbox}

  &:not(:last-child) {
    margin-bottom: 1rem;
  }
`

export const Info = styled.div`
    @media screen and (max-width: 720px) {
        font-weight: 700;
        font-style: italic;
        color: rgba(0, 0, 0, 0.65);
    }

    & > *:not(:last-child) {
        margin-right: 10px;
    }

    & button {
        font-weight: 500;

        & {
            color: #00838f;
            font-family: var(--font-lato);
        }

        @media screen and (max-width: 720px) {
            font-weight: 700;
            font-style: italic;
        }

        &:hover {
            text-decoration: underline;
        }
    }
`

export const Edit = styled.div`
  & > *:not(:last-child) {
    margin-bottom: 1rem;
  }
`

export const Children = styled.div`
  & > div:nth-child(1) {
    display: ${({ status }) => status ? 'none' : 'block' }
  }

  & > div:nth-child(2) {
    display: ${({ status }) => status ? 'block' : 'none' }
  }
`

export const Input = styled.div`
  display: table;

  & input {
    width: 100%;
    text-indent: 1rem;
    border: 1px solid rgba(0, 0, 0, 0.15);
    height: 30px;

    @media screen and (max-width: 720px) {
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 30px;
    }

    ${BorderRadius(3)}
  }
`

export const Group = styled.div`
  display: table;
  margin-top: 1rem;

  & input {
    width: 100%;
    text-indent: 1rem;
    border: 1px solid rgba(0, 0, 0, 0.15);
    height: 30px;

    ${BorderRadius(3)}

    @media (max-width: 720px) {
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 30px;
    }

    &:nth-child(1), &:nth-child(3), &:nth-child(5) {
      max-width: 72%;
    }

    &:nth-child(2), &:nth-child(4), &:nth-child(6) {
      margin-left: 1rem;
      max-width: calc(28% - 1rem);
    }

    &:not(:last-child):not(:nth-child(5)) {
      margin-bottom: 1rem;
    }
  }
`

export const Buttons = styled.div`
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const Button = `
  border: 1px solid rgba(0,0,0,0.1);
  cursor: pointer;
  height: 26px;
  padding: 0 1.6rem;
  line-height: 26px;
  box-sizing: content-box;
  vertical-align: middle;
  font-size: 1.4rem;
  font-weight: bold;
  font-family: var(--font-poppins);

  ${BorderRadius(2)}
`

export const Button1 = styled.button`
  color: #fff;
  background: -webkit-gradient(linear,left top,left bottom,from(#0db722),to(#0e9c20));

  ${Button}
`

export const Button2 = styled.button`
  color: #222;
  background: -webkit-gradient(linear,left top,left bottom,from(#ffffff),to(#ececec));
  border: 1px solid rgba(0,0,0,0.2) !IMPORTANT;

  ${Button}
`
export const Button3 = styled.button`
  color: #00838f;
  font-family: var(--font-lato);
  font-weight: 500;

  @media (max-width: 720px) {
    font-weight: 700;
    font-style: italic;
  }

  &:hover {
    text-decoration: underline;
  }
`
