import React, { useEffect, useState } from 'react';

import ElementProvider from '../ContextUpload';

import ComponentEditUpload from '../components/ComponentEditUpload';
import ComponentUploadState from '../components/ComponentUploadState';

import { Modal, Close } from '../styles/Modal';
// eslint-disable-next-line no-unused-vars
import { Content, Head, Main, Radio, Table } from '../styles/Upload';

const { jQuery: $ } = window;

function ComponentModalUpImage({ usePost, setPost, useModal, setModal, useModalThumb, setModalThumb }) {

  const modalClose = () => setModalThumb(true);

  useEffect(() => {
    if (document.querySelector('[role="modal"]')) {
      $('[role="modal"]').fadeIn(200);
      setModal(false);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [useScreen, setScreen] = useState(1);
  const [useStateNumber, setStateNumber] = useState(0);
  const [usePostCache, setPostCache] = useState(usePost);

  const { thumb = [] } = usePostCache;

  useEffect(() => {
    // # ESTE NO  O MELHOR MTODO !!
    if (JSON.stringify(usePost) !== JSON.stringify(usePostCache)) {
      setPost(usePostCache);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [thumb]);

  return (
    <Modal>
      <ElementProvider {...{ usePostCache, setPostCache, useStateNumber, setStateNumber }}>
        <Close onClick={() => modalClose()} />
        <Content>
          <Head>
            <ul>
              <Radio>
                <input type="radio" readOnly checked={useScreen === 1} />
                <label onClick={() => (useScreen !== 1) && setScreen(1)}>Subir imagem</label>
              </Radio>
              <Radio>
                <input type="radio" readOnly checked={useScreen === 2} />
                <label onClick={() => (useScreen !== 2) && setScreen(2)}>Imagem existente</label>
              </Radio>
            </ul>
          </Head>
          <Main useScreen={useScreen}>
            {useScreen === 1
              ? <ComponentEditUpload />
              : <ComponentUploadState />}
          </Main>
        </Content>
      </ElementProvider>
    </Modal>
  )
}

export default ComponentModalUpImage;
