import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';

import { PushAlert } from '../Bracael';

import ComponentModalUpImage from './ComponentModalUpImage';
import ComponentMapAwaitThumb from './ComponentMapAwaitThumb';
import ComponentMapThumbNull from './ComponentMapThumbNull';

import { Container, Head, Main, Scroll, Ul, Up } from '../styles/Thumb';

const { jQuery: $ } = window;

function ComponentEditThumb({ usePost, setPost }) {

  const [useModal, setModal] = useState(false);
  const [useModalThumb, setModalThumb] = useState(false);

  const useComponent = (() => {
    const Root = document.createElement('div');
    Root.setAttribute('role', 'modal');
    return Root;
  })();

  const { thumb = [] } = usePost;
  const altSendThumb = () => setModal(true);

  useEffect(() => {
    if (useModal) {
      ReactDOM.render(<ComponentModalUpImage {...{ usePost, setPost, useModal, setModal, useModalThumb, setModalThumb }} />, document.body.appendChild(useComponent));
    }
    else {
      if (useModalThumb) {
        $('[role="modal"]').fadeOut(300, function () { this.remove(); });
        setModalThumb(false);
      };
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal, useModalThumb]);

  const thumbNail = () => {
    const nullArray = [];
    for (var i = thumb.length + 1; i <= 8; i++) {
      nullArray.push(i);
    };
    return nullArray;
  };

  return (
    <Container>
      <Head>
        <span>{thumb.length} de 8 imagens</span>
        <Up type="button" onClick={() => thumb.length < 8 ? altSendThumb() : PushAlert('Max. de 8 imagens')} status={!(thumb.length < 8)}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 341.4 341.4" width="12" height="12" fill="#999"><polygon points="192,149.4 192,0 149.4,0 149.4,149.4 0,149.4 0,192 149.4,192 149.4,341.4 192,341.4 192,192 341.4,192 341.4,149.4" /></svg>
          <span>Upload</span>
        </Up>
      </Head>
      <Main>
        <Scroll>
          <Ul>
            {thumb.map((assoc, i) => <ComponentMapAwaitThumb key={`${i}${assoc.image_name}`} {...{ assoc, i, usePost, setPost }} />)}
            {thumbNail().map((assoc, i) => <ComponentMapThumbNull key={i} />)}
          </Ul>
        </Scroll>
      </Main>
    </Container>
  )
}

export default ComponentEditThumb;
