import React, { useEffect } from 'react';

import { Content, B1, B2, B3 } from '../styles/Over';

const { jQuery: $ } = window;

function ComponentOverLoading() {

  useEffect(() => {
    if (document.querySelector('[role="over"]')) {
      $('[role="over"]').fadeIn(200);
    };
  }, []);

  return (
    <Content>
      <B1 />
      <B2 />
      <B3 />
    </Content>
  )
}

export default ComponentOverLoading;
