import React from 'react';
import Skeleton from "react-loading-skeleton";
import { useAsync } from 'react-async';

import { storage } from '../utils/Connection';

import ComponentProductThumb from './ComponentProductThumb';
import { Div } from '../styles/Skeleton';

const defaultGravatar = 'data:image/svg+xml;base64,';

const awaitStorage = async (data) => {

  // eslint-disable-next-line no-unused-vars
  const { image_format, image_height, image_name, image_size, image_width } = data;

  async function addImageProcess(src){
    const img = new Image();
    img.src = src;
    await img.decode();
    return img.src
  }

  function onResolve(foundURL) {
    return addImageProcess(foundURL);
  };

  function onReject(error) {
    return defaultGravatar;
  };

  return await storage()
    .ref('/products/images/thumbs')
    .child(image_name + '_160x160.jpeg')
    .getDownloadURL()
    .then(onResolve, onReject)
    .catch((error) => defaultGravatar)
};

const fetchPerson = async ({ thumb }, { signal }) => {

  const [image] = thumb;
  return await awaitStorage(image);
};

function ComponentAwaitProductThumb({ thumb }) {

  const { data, error } = useAsync({ promiseFn: fetchPerson, thumb });

  if (error) return error.message
  if (data) return <ComponentProductThumb {...{ data }} />
  return (
    <Div floatChild="left">
      <Skeleton width={72} height={72} />
    </Div>
  );
};

export default ComponentAwaitProductThumb;
