export const PushAlert = data => {
  if (!document.body.contains(document.querySelector('.push-alert'))) {
    document.body.insertAdjacentHTML('beforeend', `
    <div class="push-alert">
      <p>${data}</p>
    </div>`);

    $('.push-alert').animate({
      'transform': '-70'
    }, {
      step: function (now, fx) {
        $(this).css({ "transform": "translate3d(0px, " + now + "px, 0px)" });
      },
      duration: 150,
      easing: 'linear',
      queue: false,
      complete: function () {
        setTimeout(() => {
          $(this).animate({ opacity: 0 }, {
            duration: 300,
            easing: 'linear',
            queue: false,
            complete: function () {
              this.remove()
            }
          });
        }, 1300)
      }
    }, 'linear');
  }
}

