import React from 'react';
import Skeleton from "react-loading-skeleton";
import { useHistory } from 'react-router-dom';

import { Content, Menu, List } from '../styles/Products';
import { Card, Inset, Info, Op, Title } from '../styles/Products';
import { Div } from '../styles/Skeleton';

function ComponentSkeletonProducts() {

  const history = useHistory();

  const data = ["", "", "", "", "", ""];

  return (
    <Content>
      <Menu>
        <button type="button" onClick={() => history.push('/post')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14" fill="#fff"><path d="M14,8H8v6H6V8H0V6H6V0H8V6h6V8Z"></path></svg>
          <span>Novo produto</span>
        </button>
      </Menu>
      <List>
        {data.map((data, i) => (
          <Card key={i}>
            <Inset>
              <Div floatChild="left">
                <Skeleton width={72} height={72} />
              </Div>
              <Info>
                <Op />
                <Title>
                  <Skeleton width={180} height={19} />
                </Title>
                <div>
                  <Skeleton width={59} height={21} />
                </div>
              </Info>
            </Inset>
          </Card>
        ))}
      </List>
    </Content>
  )
}

export default ComponentSkeletonProducts;