import React from 'react';

import { SnapshotStateNumber } from '../ContextUpload';

import ComponentAwaitImagesAll from '../components/ComponentAwaitImagesAll';

function ComponentUploadState() {

  // eslint-disable-next-line no-unused-vars
  const { useStateNumber, setStateNumber } = SnapshotStateNumber();

  return (useStateNumber & 1) ? (
    <ComponentAwaitImagesAll key={1} />
  ) : (
    <ComponentAwaitImagesAll key={2} />
  );
}

export default ComponentUploadState;