import React, { useEffect } from 'react';
import { useAsync } from "react-async";

import { PushAlert } from '../Bracael';
import { SnapshotPost } from '../ContextUpload';
import { storage } from '../utils/Connection';

// import ComponentEditImagesAll from './ComponentEditImagesAll';

import { Load } from '../styles/root';
import { Empty } from '../styles/ImagesModal';

const awaitStorage = async (fullPath) => await storage()
  .ref(fullPath)
  .delete()
  .then(async function() {
    PushAlert('Imagem deletada com sucesso');
    // File deleted successfully
    return await storage()
      .ref('/products/images')
      .listAll()
      .then((data) => data.items)
      .then((data) => Promise.all(data.map(async (val) => {
        return {
          assoc: await val.getMetadata(),
          url: await val.getDownloadURL()
        }
      })));
  })
  .catch(function(error) {
    PushAlert('Ups, ocorreu um erro!');
    // Uh-oh, an error occurred!
  });

const fetchPerson = async ({ useDeleteImg }, { signal }) => await awaitStorage(useDeleteImg);

function ComponentAwaitDelete({ useDeleteImg, setDeleteImg, useChecked, setChecked }) {

  // eslint-disable-next-line no-unused-vars
  const { usePostCache, setPostCache } = SnapshotPost();
  // eslint-disable-next-line no-unused-vars
  const { data, error } = useAsync({ promiseFn: fetchPerson, useDeleteImg });

  useEffect(() => {
    if (data) {

      setPostCache((prevState) => {
        const { thumb = [] } = prevState;
        return {
          ...prevState,
          thumb: thumb.filter((val, ii) => {
            const { image_name, image_format } = val;
            return data.map((value) => value.assoc.name).includes(`${image_name}.${image_format}`)
          })
        };
      });
      setDeleteImg('');
    };
    if (error) {
      setDeleteImg('');
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  return (
    <Empty>
      <Load>
        <svg aria-label="Carregando..." viewBox="0 0 100 100"><rect fill="#555555" height="6" opacity="0" rx="3" ry="3" transform="rotate(-90 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.08333333333333333" rx="3" ry="3" transform="rotate(-60 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.16666666666666666" rx="3" ry="3" transform="rotate(-30 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.25" rx="3" ry="3" transform="rotate(0 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.3333333333333333" rx="3" ry="3" transform="rotate(30 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.4166666666666667" rx="3" ry="3" transform="rotate(60 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.5" rx="3" ry="3" transform="rotate(90 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.5833333333333334" rx="3" ry="3" transform="rotate(120 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.6666666666666666" rx="3" ry="3" transform="rotate(150 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.75" rx="3" ry="3" transform="rotate(180 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.8333333333333334" rx="3" ry="3" transform="rotate(210 50 50)" width="25" x="72" y="47"></rect><rect fill="#555555" height="6" opacity="0.9166666666666666" rx="3" ry="3" transform="rotate(240 50 50)" width="25" x="72" y="47"></rect></svg>
      </Load>
      <span>Carregando...</span>
    </Empty>
  );
}

export default ComponentAwaitDelete;
