import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';

import { SnapshotOverlay } from '../Context';

import { Header } from '../styles/Header';

function ComponentHeader() {

  const { pathname } = useLocation();

  const [usePath, setPath] = useState(pathname)
  const [useTitle, setTitle] = useState('Carregando...')

  pathname !== usePath && setPath(pathname)

  if(usePath === '/'){
    useTitle !== 'Area de servio' && setTitle('Area de servio');
  }
  else if(usePath === '/settings'){
    useTitle !== 'Configuraes' && setTitle('Configuraes');
  }
  else if(usePath === '/products'){
    useTitle !== 'Meus produtos' && setTitle('Meus produtos');
  }
  else if(usePath === '/edit'){
    useTitle !== 'Novo produto' && setTitle('Novo produto');
  }
  else{
    if(usePath.split('/').includes('edit')){
      useTitle !== 'Editar novo produto' && setTitle('Editar novo produto');
    }
    else if(usePath.split('/').includes('products')){
      useTitle !== 'Editar produto' && setTitle('Editar produto');
    }
  }

  // eslint-disable-next-line no-unused-vars
  const { useOverlay, setOverlay } = SnapshotOverlay();
  const OpenMenuLateral = () => setOverlay(prevState => !prevState)

  return (
    <Header>
      <span>{useTitle}</span>
      <div>
        <button type="button" onClick={() => OpenMenuLateral()}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 515.556 515.556" fill="#777777"><path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm140.412 390.282c-88.007-44.093-194.425-45.965-284.592-4.146-30.464-34.181-49.153-79.073-49.153-128.358 0-106.61 86.723-193.333 193.333-193.333s193.333 86.723 193.333 193.333c0 51.296-20.213 97.861-52.921 132.504z" /><path d="m326.132 157.202c37.751 37.751 37.751 98.957 0 136.707s-98.957 37.751-136.707 0-37.751-98.957 0-136.707 98.956-37.751 136.707 0" /></svg>
        </button>
      </div>
    </Header>
  );
}

export default ComponentHeader;
