import React, { useState } from 'react';

import ComponentModalCodeEnter from './ComponentModalCodeEnter';
import ComponentAwaitModalCodeSearch from './ComponentAwaitModalCodeSearch';

// import { Container } from './styles';

function ComponentModalCodeStage({ data, useModal, setModal }) {

  // eslint-disable-next-line no-unused-vars
  const { receiver, date_approved, shipping_value, subtotal, tracking_code = '',
    // eslint-disable-next-line no-unused-vars
    street, number, complement, zone, district } = data[1];

  const [useStage, setStage] = useState(1);
  const [useCode, setCode] = useState(tracking_code);

  if (useStage === 1) return <ComponentModalCodeEnter {...{ data, useStage, setStage, useModal, setModal, useCode, setCode }} />
  return <ComponentAwaitModalCodeSearch {...{ assoc: data, useStage, setStage, useModal, setModal, useCode, setCode }} />
}

export default ComponentModalCodeStage;
