import React from 'react';
import ReactDOM from 'react-dom';

import { functions, auth } from './utils/Connection';

import App from './App';
import Entrar from './Entrar';

// import './index.css';

async function startDatabase() {
  return await functions().httpsCallable('getAPI')()
}

const StartConnection = () => Promise.resolve(startDatabase())

auth()
  .onAuthStateChanged(function (user) {
    if (user) {
      StartConnection()
        .then(function (data) {

          const { hostname, protocol } = new URL(window.location.href);

          // hostname === 'console.bracael.com' && protocol === 'https:' // validar para produo !IMPORTANT
          // console.log(hostname === 'localhost' && protocol === 'http:')

          return hostname === 'console.bracael.com' && protocol === 'https:' && data !== undefined ? Promise.resolve(data) : Promise.reject()
        })
        .then(function (snapshot) {
          ReactDOM.render(
            <React.StrictMode>
              <App {...snapshot.data} />
            </React.StrictMode>, document.getElementById('root'));
        })
        .catch(function () {
          ReactDOM.render(<div>ERRO 500</div>, document.getElementById('root'));
        });
    }
    else {
      ReactDOM.render(
        <React.StrictMode>
          <Entrar />
        </React.StrictMode>, document.getElementById('root'));
    };
});
