import React from 'react';

import Bracael from '../Bracael';

import ComponentMapCloth from './ComponentMapCloth';

import { Content, Title, Main } from '../styles/ClothMaterial';

function ComponentEditCloth({ usePost, setPost }) {

  return (
    <Content>
      <Title>Tecido do produto</Title>
      <Main>
        {Object.entries(Bracael.cloth_names)
          .map((data, i) => <ComponentMapCloth key={i} {...{ data, usePost, setPost }} />)}
      </Main>
    </Content>
  );
}

export default ComponentEditCloth;