import React from 'react';

import { Content, Text, Group, Input } from '../styles/Stocks';

function ComponentEditStocks({ usePost, setPost }) {

  const { qtd_stocks = {} } = usePost;
  const [ , value = 0] = Object.entries(qtd_stocks).find((val) => val[0] === '001') || [];

  const altStocks = val => {
    if (!isNaN(Number(val)) && val.replace(/[^0-9]/g, '') !== String(value) && value !== Number(val)) {
      setPost((prevState) => {
        const { qtd_stocks } = prevState;

        return {
          ...prevState,
          qtd_stocks: {
            ...qtd_stocks,
            "001": Number(val)
          }
        };
      });
    };
  };

  return (
    <Content>
      <Text>Em estoque?</Text>
      <Group>
        <Input type="text" placeholder="Quantidade"
        onInput={(e) => altStocks(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={value || ''}
        status={value} />
      </Group>
    </Content>
  );
};

export default ComponentEditStocks;
