import React from 'react';
import { useHistory } from 'react-router-dom';

import ComponentAwaitProductThumb from './ComponentAwaitProductThumb';

import { Card, Inset, Info, Op, Title, Price, Unavailable } from '../styles/Products';

function ComponentMapProduct({ data, i }) {

  const { title, category, price, thumb = [] } = data[1];
  const history = useHistory();
  const adOptions = () => alert('Estou trabalhando nesse recurso!');

  return (
    <Card>
      <Inset>
        {thumb.length ? (
          <ComponentAwaitProductThumb {...{ thumb }} />
        ) : (
          <div>No h imagem</div>
        )}
        <Info>
          <Op>
            <button type="button" onClick={() => adOptions()}>
              <svg viewBox="0 0 515.555 515.555" xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="#777777"><path d="m303.347 18.875c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /><path d="m303.347 212.209c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /><path d="m303.347 405.541c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /></svg>
            </button>
          </Op>
          <Title>
            <span>{title}</span>
          </Title>
          <div>
            <Price>{price.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Price>
            {category.includes('Unavailable') && <Unavailable>Indisponvel</Unavailable>}
          </div>
        </Info>
      </Inset>
      <button type="button" onClick={() => history.push(`/products/${data[0]}`)}></button>
    </Card>
  )
}

export default ComponentMapProduct;
