import React from 'react';

import { Checkbox } from '../styles/ClothMaterial';

function ComponentMapCloth({ data, usePost, setPost }) {

  const { cloth_material = '' } = usePost;
  const [id, name] = data;
  const altStatus = () => cloth_material === id ? setPost((prevState => {
    const assoc = { ...prevState };
    delete assoc.cloth_material;
    return assoc;
  })) : setPost((prevState => {
    return {
      ...prevState,
      cloth_material: id
    }
  }));

  return (
    <Checkbox>
      <input type="checkbox" readOnly checked={id === cloth_material} />
      <label onClick={() => altStatus()}>{name}</label>
    </Checkbox>
  );
}

export default ComponentMapCloth;