import React from 'react';
import { useParams } from 'react-router';
import { useAsync } from "react-async";

import { functions } from '../utils/Connection';

import ComponentEditProduct from '../components/ComponentEditProduct';
import ComponentSkeletonProductsId from '../components/ComponentSkeletonProductsId';

const fetchPerson = async ({ id }, { signal }) => {

  return await functions()
    .httpsCallable('getItemConnection')('public/feed/' + id)
    .then((snapshot) => snapshot.data);
}

function ProductsId() {

  const { id } = useParams();

  // eslint-disable-next-line no-unused-vars
  const { data, error } = useAsync({ promiseFn: fetchPerson, id });

  if (error) return error.message
  if (data) return <ComponentEditProduct {...{ data, id }} />
  return <ComponentSkeletonProductsId />
}

export default ProductsId;
