import React from 'react';
import ReactDOM from 'react-dom';

import { PushAlert } from '../Bracael';
import { functions } from '../utils/Connection';

import ComponentMapTracking from './ComponentMapTracking';
import ComponentOverLoading from '../components/ComponentOverLoading';

import { Container, Header, Title, Center, Content, List, Submit, Bttm } from '../styles/ECode';

const { jQuery: $ } = window;

function ComponentModalCodeSearch({ data, assoc, useStage, setStage, useModal, setModal, useCode, setCode }) {

  // eslint-disable-next-line no-unused-vars
  const { receiver, date_approved, shipping_value, subtotal, tracking_code = '',
    // eslint-disable-next-line no-unused-vars
    street, number, complement, zone, district } = assoc[1];

  const key = assoc[0];

  const createDomModal = (() => {
    const Root = document.createElement('div');
    Root.setAttribute('role', 'over');
    return Root;
  })();

  const altSubmit = () => {

    if (!document.querySelector('[role="over"]')) {
      ReactDOM.render(
        <ComponentOverLoading />,
        document.body.appendChild(createDomModal)
      );
    };

    functions().httpsCallable('set')({
      val: `order/${key}/tracking_code`,
      data: useCode
    })
    .then(function ({ data }) {
      const { status } = data;

      if (status) {
        PushAlert('Cdigo atualizado!');
      }
      else {
        PushAlert('Erro desconhecido. Tente novamente!');
      };

      if (document.querySelector('[role="over"]')) {
        $('[role="over"]').fadeOut(300, function () {
          this.remove();
          setModal(true);
        });
      };
    })
    .catch(function (error) {
      PushAlert('Erro ao chamar API!');

      if (document.querySelector('[role="over"]')) {
        $('[role="over"]').fadeOut(300, function () {
          this.remove();
          setModal(true);
        });
      };
    });
  };

  return (
    <Container>
      <Header>
        <button type="button" onClick={() => setStage(1)}>
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 451.111 451.111"><path d="m451.111 193.333h-322.222l112.778-112.777-48.333-48.333-193.334 193.333 193.333 193.333 48.333-48.333-112.777-112.778h322.222z"/></svg>
        </button>
        <Title>#{useCode}</Title>
      </Header>
      <List>
        <Center>
          <Content>
            {data.map((assoc, i) => <ComponentMapTracking key={i} {...{ assoc }} />)}
          </Content>
        </Center>
      </List>
      <Bttm>
        <Submit type="button" onClick={() => (tracking_code !== useCode) && altSubmit()} status={tracking_code !== useCode}>Enviar cdigo!</Submit>
      </Bttm>
    </Container>
  );
}

export default ComponentModalCodeSearch;
