import React from 'react';

import { Card } from '../styles/Edit';

function ComponentMapSlctSub({ data, usePost, setPost, useStage, setStage }) {

  const { id, name } = data;
  
  const altPost = () => {
    setPost((prevState) => {
      const assoc = {...prevState};
      assoc.subcategory = id;
      return assoc;
    });
    setStage(3);
  };

  return (
    <Card type="button" onClick={() => altPost()}>
      <span>{name}</span>
    </Card>
  )
}

export default ComponentMapSlctSub;