import React from 'react';

import Bracael from '../Bracael';

import ComponentPostButton from './ComponentPostButton';

import { Content, Main } from '../styles/Edit';

function ComponentPostCategory({ usePost, setPost, useStage, setStage }) {

  return (
    <Content>
      <Main>
        {Object.entries(Bracael.category_names)
          .map((data, i) => <ComponentPostButton key={i} {...{ data, usePost, setPost, useStage, setStage }} />)}
      </Main>
    </Content>
  )
}

export default ComponentPostCategory;
