import React, { useState, useRef, useEffect } from 'react';

import { PushAlert } from '../Bracael';
import { functions } from '../utils/Connection';
import { SnapshotConnection } from '../Context';

import { Modify, Title, Children, Info, Edit, Input, Buttons, Button1, Button2 } from '../styles/Modify';

const { jQuery: $ } = window;

function ComponentBasicMinvalue({ useScreenBasic, setScreenBasic }) {

  const useComponent = useRef(null);

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { business: { info: { minvalue } } } = useConn;

  // eslint-disable-next-line no-unused-vars
  const [useInput, setInput] = useState(minvalue ? minvalue.toFixed(2) : '');
  const altInput = (val) => setInput(val);

  const altSendData = async () => {
    await functions().httpsCallable('set')({
      val: 'public/business/info/minvalue',
      data: isNaN(Number(useInput)) ?
      // eslint-disable-next-line no-useless-escape
      Number(useInput.replace(/\./g, '').replace(/\,/g, '.')) : Number(useInput)
    })
      .then(function (data) {
        const { status } = data.data;
        if (status) {
          PushAlert('Configuraes salvas');
          setScreenBasic(-1);
          setConn(prevState => {
            const assoc = { ...prevState };
            assoc.business.info.minvalue = isNaN(Number(useInput)) ?
            // eslint-disable-next-line no-useless-escape
            Number(useInput.replace(/\./g, '').replace(/\,/g, '.')) : Number(useInput);
            return assoc;
          });
        }
        else {
          PushAlert('Falhou, tente novamente!');
        }
      })
      .catch(function (error) {
        console.error(error);
      });
  }

  useEffect(() => {
    const { current } = useComponent;
    current.value = useInput;
  }, [useInput]);

  useEffect(() => {
    const { current } = useComponent;
    $(current).mask('#.##0,00', { reverse: true });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useComponent]);

  return (
    <Modify>
      <Title>
        <span>Preo minimo</span>
      </Title>
      <Children status={useScreenBasic === 1}>
        <Info>
          <span>{Number(minvalue).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          <button type="button" onClick={() => setScreenBasic(1)}>Editar</button>
        </Info>
        <Edit>
          <Input>
            <input type="text" placeholder="0,00" ref={useComponent}
              onInput={(e) => altInput(e.target.value)}
              autoCapitalize="off" autoCorrect="off"
              onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
              onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
              onDrop={(e) => e.preventDefault()} autoComplete="off" />
          </Input>
          <Buttons>
            <Button1 type="button" onClick={() => altSendData()}>Salvar alteraes</Button1>
            <Button2 type="button" onClick={() => setScreenBasic(-1)}>Cancelar</Button2>
          </Buttons>
        </Edit>
      </Children>
    </Modify>
  );
}

export default ComponentBasicMinvalue;