import React, { useState } from 'react';

import { PushAlert } from '../Bracael';
import { functions } from '../utils/Connection';
import { SnapshotConnection } from '../Context';

import { Modify, Title, Children, Info, Edit, Input, Buttons, Button1, Button2 } from '../styles/Modify';

function ComponentBasicDesc({ useScreenBasic, setScreenBasic }) {

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { business: { info : { paymentName } } } = useConn;

  // eslint-disable-next-line no-unused-vars
  const [useInput, setInput] = useState(paymentName);
  const altInput = (val) => setInput(val);
  
  const altSendData = async () => {
    await functions().httpsCallable('set')({
      val: 'public/business/info/paymentName',
      data: useInput
    })
    .then(function (data) {
      const { status } = data.data;
      if (status) {
        PushAlert('Configuraes salvas');
        setScreenBasic(-1);
        setConn(prevState => {
          const assoc = {...prevState};
          assoc.business.info.paymentName = useInput;
          return assoc;
        });
      }
      else {
        PushAlert('Falhou, tente novamente!');
      }
    })
    .catch(function (error) {
      console.error(error);
    });
  }

  return (
    <Modify>
      <Title>
        <span>Descrio de compra</span>
      </Title>
      <Children status={useScreenBasic === 2}>
        <Info>
          <span>{paymentName}</span>
          <button type="button" onClick={() => setScreenBasic(2)}>Editar</button>
        </Info>
        <Edit>
          <Input>
            <input type="text" onInput={(e) => altInput(e.target.value)}
            autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off" value={useInput} />
          </Input>
          <Buttons>
            <Button1 type="button" onClick={() => altSendData()}>Salvar alteraes</Button1>
            <Button2 type="button" onClick={() => setScreenBasic(-1)}>Cancelar</Button2>
          </Buttons>
        </Edit>
      </Children>
    </Modify>
  );
}

export default ComponentBasicDesc;