import React from 'react';

import ComponentMapCardAttendance from './ComponentMapCardAttendance';

import { Container } from '../styles/Service';

function ComponentCardAttendance({ data }) {

  const { order } = data;

  return (
    <Container>
      {Object.keys(order).length
        ? Object.entries(order).map((assoc, i) => <ComponentMapCardAttendance key={assoc[0]} {...{ data: assoc, i, assoc: data }} />)
        : <small>Por enquanto, no h nada por aqui.</small>}
    </Container>
  )
}

export default ComponentCardAttendance;
