import React, { useState } from 'react';
import { HashRouter } from 'react-router-dom';

import ComponentPushmenu from './components/ComponentPushmenu';
import ComponentMain from './components/ComponentMain';
import ComponentHiddenMenu from './components/ComponentHiddenMenu';
import ComponentOverlay from './components/ComponentOverlay';
import ComponentSwipe from './components/ComponentSwipe';

import ElementProvider from './Context';

function App(snapshot) {

  const [useConn, setConn] = useState(snapshot);
  const [useOverlay, setOverlay] = useState(false);
  const [useSwipe, setSwipe] = useState(false);

  return (
    <HashRouter>
      <ElementProvider {...{ useConn, setConn, useOverlay, setOverlay, useSwipe, setSwipe }}>
        <ComponentPushmenu />
        <ComponentMain />
        <ComponentHiddenMenu />
        <ComponentOverlay />
        <ComponentSwipe />
      </ElementProvider>
    </HashRouter>
  );
}

export default App;
