import React, { useState, useEffect, useRef } from 'react';

import { SnapshotSwipe } from '../Context';

import { Swipe } from '../styles/Swipe';

const { jQuery: $ } = window;

function ComponentSwipe({ data }) {

  // eslint-disable-next-line no-unused-vars
  const { useSwipe, setSwipe } = SnapshotSwipe();
  const useComponentSwipe = useRef(null);
  const [menuOpenAndClose, setOpenAndClose] = useState(false)

  useEffect(() => {
    const { current } = useComponentSwipe
    const OpenMenuLatel = () => $(current).fadeIn(300);

    menuOpenAndClose && OpenMenuLatel()

  }, [menuOpenAndClose])

  useEffect(() => {
    const { current } = useComponentSwipe
    const CloseMenuLatel = () => $(current).fadeOut(300, () => setOpenAndClose(false));

    useSwipe ? setOpenAndClose(true) : CloseMenuLatel()
  }, [useSwipe])

  return menuOpenAndClose && <Swipe ref={useComponentSwipe} onClick={() => setSwipe(false)} />;
}

export default ComponentSwipe;
