import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../utils/Connection';

import ComponentCardAttendance from '../components/ComponentCardAttendance';
import ComponentSkeletonCardAttendance from '../components/ComponentSkeletonCardAttendance';

const fetchPerson = async ({ signal }) => {

  return {
    order: await functions().httpsCallable('getItemConnection')('order')
      .then((data) => data.data),
    feed: await functions().httpsCallable('getItemConnection')('public/feed')
      .then((data) => data.data)
  }
};

function Service() {

  const { data, error } = useAsync({ promiseFn: fetchPerson })

  if (error) return error.message
  if (data) return <ComponentCardAttendance {...{ data }} />
  return <ComponentSkeletonCardAttendance />
};

export default Service;
