import React from 'react';
import Skeleton from "react-loading-skeleton";
import { useAsync } from 'react-async';

import { storage } from '../utils/Connection';

import ComponentMapThumb from './ComponentMapThumb';

import { Div } from '../styles/Skeleton';

const defaultGravatar = 'data:image/svg+xml;base64,';

const awaitStorage = async (data) => {

  const { image_name } = data;

  async function addImageProcess(src){
    const img = new Image();
    img.src = src;
    await img.decode();
    return img.src
  }

  async function onResolve(foundURL) {

    return {
      ...data,
      url: await addImageProcess(foundURL)
    };
  };

  function onReject(error) {
    return defaultGravatar;
  };

  return await storage()
    .ref('/products/images/thumbs')
    .child(image_name + '_160x160.jpeg')
    .getDownloadURL()
    .then(onResolve, onReject)
    .catch((error) => defaultGravatar)
};

const fetchPerson = async ({ assoc }, { signal }) => {

  return await awaitStorage(assoc);
};

function ComponentMapAwaitThumb({ assoc, i, usePost, setPost }) {

  const { data, error } = useAsync({ promiseFn: fetchPerson, assoc });

  if (error) return error.message
  if (data) return <ComponentMapThumb {...{ data, i, usePost, setPost }} />
  return (
    <Div floatChild="left">
      <Skeleton width={72} height={72} />
    </Div>
  );
};

export default ComponentMapAwaitThumb;
