import React from 'react';

import Bracael from '../Bracael';

import { Content, Title, Main, Select, Option } from '../styles/RingNumber';

function ComponentEditSizePulseira({ usePost, setPost }) {

  // eslint-disable-next-line no-unused-vars
  const { id_size = '' } = usePost;

  const altChange = (val) => setPost((prevState) => {
    return {
      ...prevState,
      id_size: val
    }
  });

  return (
    <Content>
      <Title>Numerao de produto</Title>
      <Main>
        <Select required onChange={(e) => altChange(e.target.value)} value={id_size}>
          {[["", "Selecione o tamanho"], ...Object.entries(Bracael.stocks)
          .filter(([id, name]) => Bracael.size_pulseira.includes(id))]
          .map(([id, name], i) => <Option key={i} value={id || ""} disabled={id ? '' : 'disabled'}>{name}</Option>)}
        </Select>
      </Main>
    </Content>
  )
}

export default ComponentEditSizePulseira;
