import React, { useState } from 'react';

import { PushAlert } from './Bracael';
import { auth } from './utils/Connection';
import ComponentProvider from './ContextEnter';

import ComponentShowAndHidden from './components/ComponentShowAndHidden';

import { Enter, Marca, Group, Input, Label, Bottom, Forgot, Login } from './styles/Enter';

function Entrar() {

  const [useToggle, setToggle] = useState(false);
  const [useInputEmail, setInputEmail] = useState('');
  const [useInputPassword, setInputPassword] = useState('');

  const AccountEntrar = (event) => {
    event.preventDefault();

    auth()
      .signInWithEmailAndPassword(useInputEmail, useInputPassword)
      .then(() => PushAlert("Seja bem-vindo!"))
      .catch(function () {
        alert("E-mail ou senha invalidos");
      });
  };

  return (
    <ComponentProvider {...{ useToggle, setToggle }}>
      <Enter>
        <div>
          <Marca>
            <section></section>
            <span>Use uma conta Bracael</span>
          </Marca>
          <form autoComplete="off">
            <Group>
              <Input type="email" autoComplete="off" autoCapitalize="off" autoCorrect="off" hasText={useInputEmail} onChange={(event) => setInputEmail(event.target.value)} value={useInputEmail} />
              <Label>E-mail</Label>
            </Group>
            <Group>
              <Input type={useToggle ? "text" : "password"} hasText={useInputPassword} onChange={(event) => setInputPassword(event.target.value)} value={useInputPassword} />
              <ComponentShowAndHidden />
              <Label>Senha</Label>
            </Group>
            <Bottom>
              <Forgot type="button" onClick={() => alert("Entre em contato conosco pelo WhatsApp ou Instagram!")}>Esqueceu a sua senha?</Forgot>
              <Login type="submit" onClick={(e) => AccountEntrar(e)}>Fazer login</Login>
            </Bottom>
          </form>
        </div>
      </Enter>
    </ComponentProvider>
  );
}

export default Entrar;
