import styled from 'styled-components';

export const Overlay = styled.div`
  width: 100vw;
  height: 100%;
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;

  & {
    backdrop-filter: blur(2px);
    background: rgba(255, 255, 255, 0.6);
  }
`
