import React, { useRef, useEffect } from 'react';

import { Group, Input, Label } from '../styles/Post';

const { jQuery: $ } = window;

function ComponentEditPrice({ usePost, setPost }) {

  const useComponent = useRef(null);
  const { price = 0 } = usePost;

  const InputPrice = (val) => setPost((prevState) => {
    return {
      ...prevState,
      price: isNaN(Number(val)) ?
      // eslint-disable-next-line no-useless-escape
      Number(val.replace(/\./g, '').replace(/\,/g, '.')) : Number(val)
    };
  });

  useEffect(() => {
    const { current } = useComponent;
    current.value = price ? price.toFixed(2) : '';
    $(current).mask('#.##0,00', { reverse: true });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useComponent]);

  return (
    <Group>
      <Input type="text" placeholder="0,00" ref={useComponent}
        onInput={(e) => InputPrice(e.target.value)} status={true}
        autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off" />
      <Label>R$</Label>
    </Group>
  )
}

export default ComponentEditPrice;
