import React from 'react';

import { Content, Title, Main, Status } from '../styles/SortClass';

function ComponentEditSortClass({ usePost, setPost }) {

  const { sort_child = false } = usePost;

  const altSort = () => setPost((prevState) => {
    const { sort_child = false } = prevState;
    return {
      ...prevState,
      sort_child: !sort_child
    }
  });

  return (
    <Content>
      <Title>Produto infantil?</Title>
      <Main>
        <Status>
          <input type="checkbox" readOnly checked={sort_child} />
          <label onClick={() => altSort()}>{sort_child ? 'Infantil' : 'Adulto'}</label>
        </Status>
      </Main>
    </Content>
  )
}

export default ComponentEditSortClass;
