import React from 'react';

import { PushAlert } from '../Bracael';
import { functions } from '../utils/Connection';
import { SnapshotConnection } from '../Context';

import { Modify, Title, Status } from '../styles/Modify';

function ComponentBasicCupom() {

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { business: { offCupom } } = useConn;

  const adChange = async () => {
    await functions().httpsCallable('set')({
      val: 'public/business/offCupom',
      data: !offCupom
    })
    .then(function (data) {
      const { status } = data.data;
      if (status) {
        PushAlert('Configuraes salvas');
        setConn(prevState => {
          const assoc = {...prevState};
          assoc.business.offCupom = !offCupom;
          return assoc;
        });
      }
      else {
        PushAlert('Falhou, tente novamente!');
      }
    })
    .catch(function (error) {
      console.error(error);
    });
  }

  return (
    <Modify>
      <Title>
        <span>Disponibilizar cupom?</span>
      </Title>
      <div>
        <Status>
          <input type="checkbox" onChange={() => adChange()} checked={offCupom} />
          <label onClick={() => adChange()}>{offCupom ? 'Sim' : 'No'}</label>
        </Status>
      </div>
    </Modify>
  );
}

export default ComponentBasicCupom;