export const LabelCheckbox = `
  & ~ label:before {
    content: '';
    background: #f4f4f4;
    border-radius: 100%;
    border: 1px solid #b4b4b4;
    display: inline-block;
    width: 1.4em;
    height: 1.4em;
    position: relative;
    margin-right: 1em;
    vertical-align: top;
    cursor: pointer;
    text-align: center;
    -webkit-transition: all 250ms ease;
    transition: all 250ms ease;
  }

  &:checked ~ label:before {
    background-color: #3197EE;
    box-shadow: inset 0 0 0 3px #f4f4f4;
  }

  &:focus ~ label:before {
    outline: none;
    border-color: #3197EE;
  }

  &:disabled ~ label:before {
    box-shadow: inset 0 0 0 4px #f4f4f4;
    border-color: #b4b4b4;
    background: #b4b4b4;
  }

  & ~ label:empty:before {
    margin-right: 0;
  }
`

