import React, { useState } from 'react';

// eslint-disable-next-line no-unused-vars
import Bracael from '../Bracael';

import ComponentBasicTitle from '../components/ComponentBasicTitle';
import ComponentBasicMinvalue from '../components/ComponentBasicMinvalue';
import ComponentBasicDesc from '../components/ComponentBasicDesc';
import ComponentBasicCupom from '../components/ComponentBasicCupom';

import { Content, Title } from '../styles/Settings';

function Settings() {

  const [useScreenBasic, setScreenBasic] = useState(-1);

  return (
    <Content>
      <div>
        <Title>
          <span>Bsico</span>
        </Title>
        <div>
          <ComponentBasicTitle {...{ useScreenBasic, setScreenBasic }} />
          <ComponentBasicMinvalue {...{ useScreenBasic, setScreenBasic }} />
          <ComponentBasicDesc {...{ useScreenBasic, setScreenBasic }} />
          <ComponentBasicCupom />
        </div>
      </div>
    </Content>
  )
}

export default Settings;
