import React from 'react';

import ComponentMapReadyImage from './ComponentMapReadyImage';
import ComponentAwaitSendImage from './ComponentAwaitSendImage';

function ComponentImageStage({ useLoadImg, setLoadImg, useStage, setStage }) {

  return useStage ?
    <ComponentAwaitSendImage {...{ useLoadImg, setLoadImg }} /> : useLoadImg.map((data, i) => <ComponentMapReadyImage key={i} {...{ data, i, useLoadImg, setLoadImg }} />);
}

export default ComponentImageStage;
