import React from 'react';

import ComponentMapImageAllSelect from './ComponentMapImageAllSelect';
import ComponentMapImageAllDelete from './ComponentMapImageAllDelete';

import { Mainly, Main, Head, Status, Name, Empty } from '../styles/ImagesModal';

function ComponentEditImagesAll({ data, useDeleteImg, setDeleteImg, useChecked, setChecked }) {

  return data.length ? (
    <Mainly>
      <Head>
        <Status>
          <input type="checkbox" readOnly checked={useChecked} />
          <label onClick={() => setChecked((prevState) => !prevState)}>{useChecked ? 'Selecionar' : 'Deletar'}</label>
        </Status>
        <Name>{data.length} imagens em total</Name>
      </Head>
      <Main>
        {useChecked ? 
          data.map((data, i) => <ComponentMapImageAllSelect key={i} {...{ data }} />) :
          data.map((data, i) => <ComponentMapImageAllDelete key={i} {...{ data, useDeleteImg, setDeleteImg }} />)}
      </Main>
    </Mainly>
  ) : <Empty>Por enquanto, no h nada por aqui.</Empty>;
}

export default ComponentEditImagesAll;