import React, { useEffect, useRef, useState } from 'react';

import Bracael from '../Bracael';

import ComponentMapSizeNumber from '../components/ComponentMapSizeNumber';
import ComponentMapSizeLetter from '../components/ComponentMapSizeLetter';

import { Content, Head, Switch, Main } from '../styles/Size';

function ComponentEditSizeLetter({ usePost, setPost }) {

  const useStatus = useRef(false);

  const { subcategory, qtd_stocks = {} } = usePost;
  const { status = Bracael.p[2].includes(subcategory), ...stocks } = qtd_stocks || {};
  const [usePass, setPass] = useState(false);

  const [useNumber, setNumber] = useState(status ? {} : stocks);
  const [useLetter, setLetter] = useState(stocks ? stocks : {});

  const altLabel = () => setPass(true);

  useEffect(() => {
    if (usePass) {
      setPass(false);
      setPost((prevState) => {
        const { qtd_stocks = {} } = prevState;
        // eslint-disable-next-line no-unused-vars
        const { status = Bracael.p[2].includes(subcategory), ...stocks } = qtd_stocks || {};

        const assoc = {...prevState};
        if (status) {
          assoc.qtd_stocks = useNumber;
        }
        else {
          assoc.qtd_stocks = useLetter;
        };
        assoc.qtd_stocks.status = !status;
        return assoc;
      });
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [usePass]);

  useEffect(() => {
    if (useStatus.current) {
      if (status) {
        setLetter(stocks ? stocks : {});
      }
      else {
        setNumber(status ? {} : stocks);
      };
      useStatus.current = false;
    }
    else {
      useStatus.current = true;
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [stocks]);

  return (
    <Content>
      <Head>
        <span>Produtos em estoque</span>
        <Switch>
          <input type="checkbox" readOnly checked={status} />
          <label onClick={() => altLabel()}></label>
        </Switch>
      </Head>
      <Main>
        {status
          ? Bracael.size_letter.map((data, i) => <ComponentMapSizeLetter key={i} {...{ data, usePost, setPost }} />)
          : Bracael.size_number.map((data, i) => <ComponentMapSizeNumber key={i} {...{ data, usePost, setPost }} />)}
      </Main>
    </Content>
  )
}

export default ComponentEditSizeLetter;
