import React from 'react';

import { Li, L, R, Line, Data, Time, Status, Local } from '../styles/Tracking';

function FormataStringData(data) {
  var dia  = data.split("/")[0];
  var mes  = data.split("/")[1];
  var ano  = data.split("/")[2];

  return ("0"+dia).slice(-2) + '.' + ("0"+mes).slice(-2) + '.' + ano;
  // Utilizo o .slice(-2) para garantir o formato com 2 digitos.
}

function ComponentMapTracking({ assoc }) {

  // eslint-disable-next-line no-unused-vars
  const { data, destino, hora, origem, status, local } = assoc;

  return (
    <Li>
      <L>
        <Data>{FormataStringData(data)}</Data>
        <Time>{hora}</Time>
      </L>
      <Line />
      <R>
        <Status>{status}</Status>
        <Local>{local
          ? local
          : destino}</Local>
      </R>
    </Li>
  );
}

export default ComponentMapTracking;
