import React from 'react';

import { PushAlert } from '../Bracael';

import { Content, Title, Main, Color, Label } from '../styles/Color';
import { Group } from '../styles/Post';

function ComponentEditColor({ usePost, setPost }) {

  const { colors = [] } = usePost;

  const altSetColor = (val) => {
    if (colors.length < 2) {
      const assoc = { ...usePost };
      assoc.colors = [ ...colors ];
      
      if (colors.length === 1 && assoc.colors.includes(val)) {
        delete assoc.colors;
      }
      else {
        assoc.colors.includes(val) ?
        assoc.colors.splice(assoc.colors.indexOf(val), 1) : assoc.colors.push(val);
      }
      setPost(assoc);
    }
    else {
      if (colors.includes(val)) {
        const assoc = { ...usePost };
        assoc.colors.splice(assoc.colors.indexOf(val), 1);
        setPost(assoc);
      }
      else {
        PushAlert('Tenha no mximo de 2 colores!');
      };
    };
  };

  return (
    <Group>
      <Content>
        <Title>Cores do produto</Title>
        <Main>
          <div>
            <Color type="checkbox" hexColor="red" readOnly checked={colors.includes('red')} />
            <Label onClick={() => altSetColor('red')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="green" readOnly checked={colors.includes('green')} />
            <Label onClick={() => altSetColor('green')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="yellow" readOnly checked={colors.includes('yellow')} />
            <Label onClick={() => altSetColor('yellow')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="black" readOnly checked={colors.includes('black')} />
            <Label onClick={() => altSetColor('black')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="orange" readOnly checked={colors.includes('orange')} />
            <Label onClick={() => altSetColor('orange')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="pink" readOnly checked={colors.includes('pink')} />
            <Label onClick={() => altSetColor('pink')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="purple" readOnly checked={colors.includes('purple')} />
            <Label onClick={() => altSetColor('purple')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="blue" readOnly checked={colors.includes('blue')} />
            <Label onClick={() => altSetColor('blue')} />
          </div>
          <div>
            <Color type="checkbox" hexColor="cyan" readOnly checked={colors.includes('cyan')} />
            <Label onClick={() => altSetColor('cyan')} />
          </div>
        </Main>
      </Content>
    </Group>
  )
}

export default ComponentEditColor;