import React from 'react';

import Bracael from '../Bracael';

import ComponentMapSlctSub from './ComponentMapSlctSub';

import { Content, Head, Title, Main } from '../styles/Edit';

function ComponentPostSubcategory({ useStage, setStage, usePost, setPost }) {

  const { category } = usePost;

  return (
    <Content>
      <Head>
        <Title>Qual o produto?</Title>
        <button type="button" onClick={() => setStage(1)}>Voltar?</button>
      </Head>
      <Main>
        {Bracael.organize_names[category]
          .map((val) => {
            return {
              id: val,
              name: Bracael.subcategory_names[val]
            }
          })
          .filter((data) => !!data.name)
          .map((data, i) => <ComponentMapSlctSub key={i} {...{ data, usePost, setPost, useStage, setStage }} />)}
      </Main>
    </Content>
  )
}

export default ComponentPostSubcategory;