import React, { useState } from 'react';

import ComponentAwaitDelete from '../components/ComponentAwaitDelete';
import ComponentEditImagesAllLoad from '../components/ComponentEditImagesAllLoad';

function ComponentAwaitImagesAll(){

  const [useChecked, setChecked] = useState(true);
  const [useDeleteImg, setDeleteImg] = useState('');

  return useDeleteImg ? (
    <ComponentAwaitDelete {...{ useDeleteImg, setDeleteImg, useChecked, setChecked }} />
  ) : (
    <ComponentEditImagesAllLoad {...{ useDeleteImg, setDeleteImg, useChecked, setChecked }} />
  );
}

export default ComponentAwaitImagesAll;