import React from 'react';

import { Card } from '../styles/Edit';

function ComponentPostButton({ data, usePost, setPost, useStage, setStage }) {

  const [id, name] = data;

  const altPost = () => {
    setPost((prevState) => {
      const assoc = {...prevState};
      assoc.category = id;
      return assoc;
    });
    setStage(2);
  };

  return (
    <Card type="button" onClick={() => altPost()}>
      <span>{name}</span>
    </Card>
  )
}

export default ComponentPostButton;