import React from 'react';

import { Content, Title, Main, Group, Input, Label, Text } from '../styles/SizeLength';

function ComponentEditSizeLength({ usePost, setPost }) {

  const { size_length = 0 } = usePost;

  const altLength = (val) => {
    if (!isNaN(Number(val)) && val.replace(/[^0-9]/g, '') !== String(size_length) && size_length !== Number(val)) {
      setPost((prevState) => {
        return {
          ...prevState,
          size_length: Number(val)
        }
      });
    };
  };

  return (
    <Content>
      <Title>Comprimento do produto</Title>
      <Main>
        <Group>
          <Input type="text" placeholder="0"
            onInput={(e) => altLength(e.target.value)} autoCapitalize="on" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off"
            value={size_length ? size_length : ''}
            status={true} />
          <Label>Centmetros</Label>
        </Group>
        <Group>
          <Text>{size_length / 100} M</Text>
        </Group>
      </Main>
    </Content>
  )
}

export default ComponentEditSizeLength;
