import React from 'react';
import ReactDOM from 'react-dom';

import { PushAlert } from '../Bracael';
import { SnapshotPost } from '../ContextUpload';

import ComponentModalSimple from './ComponentModalSimple';

import { Content, Status } from '../styles/ThumbMap';

// HELP remove o formato da string de nome da imagem
const ComaRemove = (val) => {
  const nameSplit = [...val.split('.')];
  nameSplit.pop();
  return nameSplit.join();
}

// HELP pega o formato original da imagem
const GetFormat = (val) => {
  return [...val.split('.')].pop();
}

function ComponentMapImageAllSelect({ data }) {

  const { assoc, url } = data;

  const createDomModal = (() => {
    const Root = document.createElement('div')
    Root.setAttribute('role', 'modaly');
    return Root;
  })();

  const { name, size } = assoc;
  const { usePostCache, setPostCache } = SnapshotPost();
  const { thumb = [] } = usePostCache;

  const altSelectImage = () => Outward({
    title: 'Confirmar imagem',
    message: 'Ei, voc quer adicionar esta imagem para este produto?',
    button: ['Cancelar', 'Adicionar'],
    buttonColor: '#19c556',
    action: function() {
      setPostCache((prevState) => {
        const { thumb = [] } = prevState;
        (thumb.length === 8) ? PushAlert('Maxmo de 8 imagens!') : PushAlert('Imagen adicionada!');
        const assoc = {
          ...prevState,
          thumb: [
            ...thumb,
            ...[{
              image_size: size,
              image_name: ComaRemove(name),
              image_format: GetFormat(name)
            }]
          ]
        };
        return thumb.length < 8 ? assoc : prevState;
      });
    }
  });

  const Outward = data => ReactDOM.render(<ComponentModalSimple {...data} />, document.body.appendChild(createDomModal));

  const IMGStatus = () => thumb.map((data) => `${data.image_name}.${data.image_format}`).includes(name);

  return (
    <Content style={{ backgroundImage: "url(" + url + ")" }} onClick={() => IMGStatus() ? PushAlert('Esta imagem j foi selecionada!') : altSelectImage()}>
      <img src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw==" height="100%" width="100%" alt="" />
      <Status ckecked={IMGStatus()} />
    </Content>
  );
}

export default ComponentMapImageAllSelect;
