import React, { useEffect, useRef, useState } from 'react';

import { SnapshotOverlay } from '../Context';

import { Overlay } from '../styles/Overlay';

const { jQuery: $ } = window;

function ComponentOverlay() {
  // eslint-disable-next-line no-unused-vars
  const { useOverlay, setOverlay } = SnapshotOverlay();
  const useComponentSwipe = useRef(null);
  const [menuOpenAndClose, setOpenAndClose] = useState(false)

  useEffect(() => {
    const { current } = useComponentSwipe
    const CloseMenuLatel = () => {
      document.querySelector('#root').style.transform = ("translate3d(-256px, 0, 0)")
      $(current).fadeIn(300);
    }
    menuOpenAndClose && CloseMenuLatel()
  }, [menuOpenAndClose])

  useEffect(() => {
    const { current } = useComponentSwipe
    const CloseMenuLatel = () => {
      document.querySelector('#root').style.transform !== '' &&
        document.querySelector('#root').removeAttribute('style')
      $(current).fadeOut(300, function () {
        setOpenAndClose(false)
      });
    }

    useOverlay ? setOpenAndClose(true) : CloseMenuLatel()
  }, [useOverlay])

  return menuOpenAndClose && <Overlay ref={useComponentSwipe} onClick={() => setOverlay(false)} />
}

export default ComponentOverlay;
