import React, { useEffect } from 'react';
import { useAsync } from 'react-async';

import { storage } from '../utils/Connection';
import { SnapshotStateNumber } from '../ContextUpload';

import ComponentMapLoadImage from './ComponentMapLoadImage';
import ComponentMapSucessImage from './ComponentMapSucessImage';

const fetchPerson = async ({ useLoadImg }, { signal }) => {
  
  return await Promise.all(useLoadImg.map(async (data, i) => {

    const { file, type } = data;
    const { name } = file;

    return await storage()
      .ref(`/products/images/${(+new Date()) + '-' + name}`)
      .put(file, {
        contentType: type
      })
      .then(snapshot => snapshot.ref.getMetadata());
  }));
}

function ComponentAwaitSendImage({ useLoadImg, setLoadImg }) {

  // eslint-disable-next-line no-unused-vars
  const { useStateNumber, setStateNumber } = SnapshotStateNumber()
  const { data, error } = useAsync({ promiseFn: fetchPerson, useLoadImg });

  useEffect(() => {
    if (data) {
      setStateNumber((prevState) => prevState+1);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  if (error) return error.message
  if (data) return useLoadImg.map((data, i) => <ComponentMapSucessImage key={i} {...{ data }} />);
  return useLoadImg.map((data, i) => <ComponentMapLoadImage key={i} {...{ data }} />);
}

export default ComponentAwaitSendImage;