import React from 'react';

// import { Container } from './styles';

function ComponentMapModalCart({ assoc, i, cart }) {

  const { title, price } = assoc[1];
  // eslint-disable-next-line no-unused-vars
  const { amount = 1, moment } = cart.find((val) => val.id === assoc[0]);

  return (
    <>
      <li>
        <span>{new Array(3 + 1 - (i + '').length).join('0') + ++i}</span>
        <span>{title}</span>
        <span>{amount}un</span>
        <span>{Number(price * amount).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
      </li>
    </>
  )
}

export default ComponentMapModalCart;
