import React, { useEffect } from 'react';

import ComponentMapModalCart from './ComponentMapModalCart';

import { Modal, Close, Content } from '../styles/Modal';
import { Comanda, Strong, List, Subtotal, Frete, Footer } from '../styles/ModalCart';

const { jQuery: $ } = window;

function ComponentModalCart({ data, useModal, setModal, assoc }) {

  const modalClose = () => setModal(true);

  useEffect(() => {
    if (document.querySelector('[role="modal"]')) {
      $('[role="modal"]').fadeIn(200);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const { feed } = assoc;

  // eslint-disable-next-line no-unused-vars
  const { cart, phone, receiver, date_approved, shipping_value, subtotal, address_optional, address_number, street, zone, district } = data[1];

  return (
    <Modal>
      <Close onClick={() => modalClose()} />
      <Content>
        <Comanda>
          <Strong>
            <span>#</span>
            <span>DESCRIO</span>
            <span>QTD.</span>
            <span>TOTAL</span>
          </Strong>
          <List>
            {Object.entries(feed)
              .filter((assoc) => cart.map((val) => val.id).includes(assoc[0]))
              .map((assoc, i) => <ComponentMapModalCart key={assoc[0]} {...{ assoc, i, cart }} />)}
            <Subtotal>
              <span>SUBTOTAL</span>
              <span>{Number(subtotal).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
            </Subtotal>
          </List>
          <Frete>
            <span><small>ACRESCIMO</small>VALOR DA ENTREGA</span>
            <span>{Number(shipping_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Frete>
          <Footer>
            <b>TOTAL A PAGAR</b>
            <span>{Number(subtotal + shipping_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Footer>
        </Comanda>
      </Content>
    </Modal>
  );
}

export default ComponentModalCart;
