import styled from "styled-components";

import Bracael from '../Bracael';

import { BorderRadius } from "./root";

export const Content = styled.div`
  width: 100%;
  background: #efefef;
  padding: 1.4rem;

  ${BorderRadius(3)}

  & > *:not(:last-child) {
    margin-bottom: 1.4rem;
  }
`

export const Title = styled.span`
  display: block;
  text-transform: uppercase;
  font-weight: 900;
`

export const Main = styled.div`
  display: flex;

  & > *:not(:last-child) {
    margin-right: 1rem;
  }
`

export const Color = styled.input`
  & ~ label:before {
    content: '';
    background: ${({ hexColor }) => Bracael.product_color[hexColor] ? Bracael.product_color[hexColor] : '#f4f4f4' };
    box-shadow: inset 0 0 0 1px rgb(0,0,0,0.2);
    display: inline-block;
    width: 26px;
    height: 26px;
    position: relative;
    margin-right: 1em;
    vertical-align: top;
    cursor: pointer;
    text-align: center;
    -webkit-transition: all 250ms ease;
    transition: all 250ms ease;

    ${BorderRadius('50%')}
  }

  &:checked ~ label:before {
    box-shadow: 0 0 0 2px #fff,0 0 0 3px #3197ee;
  }

  &:focus ~ label:before {
    outline: none;
    border-color: #3197EE;
  }

  &:disabled ~ label:before {
    box-shadow: inset 0 0 0 4px #f4f4f4;
    border-color: #b4b4b4;
    background: #b4b4b4;
  }

  & ~ label:empty:before {
    margin-right: 0;
  }
`

export const Label = styled.label`
  display: flex;

  &:before {
    float: left;
  }
`
