import React, { useState } from 'react';

import { SnapshotConnection } from '../Context';

import { Content, Text, Values } from '../styles/Installment';

function ComponentEditInstallment({ usePost, setPost }) {

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { max_installment = 1 } = useConn.business.info || {} ;

  const [useAnimate, setAnimate] = useState(false);

  const { installment = 1 } = usePost;

  const altAmount = val => setPost((prevState) => {
    return {
      ...prevState,
      installment: val
    }
  });

  // eslint-disable-next-line no-unused-vars
  const altTransition = () => {
    setAnimate(true);
    setTimeout(() => setAnimate(false), 500);
  };

  const altDecrement = () => installment > 1 ? altAmount(installment-1) : altTransition();
  const altIncrement = () => installment < max_installment ? altAmount(installment+1) : altTransition();

  return (
    <Content>
      <Text>Parcelado em at?</Text>
      <Values animeBtn={useAnimate}>
        <button type="button" onClick={() => altDecrement()}>-</button>
        <input type="text" readOnly value={installment} />
        <button type="button" onClick={() => altIncrement()}>+</button>
      </Values>
    </Content>
  )
}

export default ComponentEditInstallment;
