import React, { useEffect } from 'react';

import ComponentModalCodeStage from './ComponentModalCodeStage';

import { Modal, Close, Content } from '../styles/Modal';
import { Contently } from '../styles/ECode';

const { jQuery: $ } = window;

function ComponentModalCode({ data, useModal, setModal }) {

  const modalClose = () => setModal(true);

  useEffect(() => {
    if (document.querySelector('[role="modal"]')) {
      $('[role="modal"]').fadeIn(200);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Modal>
      <Close onClick={() => modalClose()} />
      <Content>
        <Contently>
          <ComponentModalCodeStage {...{ data, useModal, setModal }} />
        </Contently>
      </Content>
    </Modal>
  );
};

export default ComponentModalCode;
