import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../utils/Connection';

import ComponentProductList from '../components/ComponentProductList';
import ComponentSkeletonProducts from '../components/ComponentSkeletonProducts';

const fetchPerson = async ({ signal }) => {

  return await functions()
    .httpsCallable('getItemConnection')('public/feed')
    .then((snapshot) => snapshot.data);
}

function Products() {

  // eslint-disable-next-line no-unused-vars
  const { data, error } = useAsync({ promiseFn: fetchPerson });

  if (error) return error.message
  if (data) return <ComponentProductList {...{ data }} />
  return <ComponentSkeletonProducts />
}

export default Products;
