import React from 'react';

import { Modal, Close, Content, Title, Footer, Message } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalSimple({ title, message, button, buttonColor, onClick = true, action }) {

  const modalClose = () => $('[role="modaly"]').fadeOut(300, function () { this.remove(); });

  const modalConfirm = () => {
    action();
    modalClose();
  }

  $('[role="modaly"]').fadeIn(200);

  return (
    <Modal component={onClick}>
      {onClick && <Close onClick={() => modalClose()} />}
      <Content>
        <Title>
          <span>{title}</span>
        </Title>
        <Message>{message}</Message>
        <Footer buttonColor={buttonColor && buttonColor}>
          {button.length === 1 ?
            <button type="button" tabIndex="0" onClick={() => modalClose()}>{button[0]}</button>
            :
            <>
              <button type="button" tabIndex="0" onClick={() => modalClose()}>{button[0]}</button>
              <button type="button" tabIndex="0" onClick={() => modalConfirm()}>{button[1]}</button>
            </>}
        </Footer>
      </Content>
    </Modal>
  )
}

export default ComponentModalSimple;
