import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { useHistory } from 'react-router';

import Bracael, { PushAlert } from '../Bracael';
import { functions } from '../utils/Connection';

import ComponentEditThumb from '../components/ComponentEditThumb';
import ComponentEditTitle from '../components/ComponentEditTitle';
import ComponentEditPrice from '../components/ComponentEditPrice';
import ComponentEditDesciption from '../components/ComponentEditDesciption';
import ComponentEditColor from '../components/ComponentEditColor';
import ComponentEditStocks from '../components/ComponentEditStocks';
import ComponentEditSizeNumber from '../components/ComponentEditSizeNumber';
import ComponentEditSizeLetter from '../components/ComponentEditSizeLetter';
import ComponentEditCloth from '../components/ComponentEditCloth';
import ComponentEditRingNumber from '../components/ComponentEditRingNumber';
import ComponentEditInstallment from '../components/ComponentEditInstallment';
import ComponentEditSizeLength from '../components/ComponentEditSizeLength';
import ComponentEditSortClass from '../components/ComponentEditSortClass';
import ComponentEditSizePulseira from '../components/ComponentEditSizePulseira';
import ComponentEditSizeColar from '../components/ComponentEditSizeColar';
import ComponentOverLoading from '../components/ComponentOverLoading';

import { Container, Basic, Menu, Name, Category, P, Buttons, Go, Simple, Main } from '../styles/Post';

const { jQuery: $ } = window;

function ComponentEditProduct({ data, id }) {

  const createDomModal = (() => {
    const Root = document.createElement('div');
    Root.setAttribute('role', 'over');
    return Root;
  })();

  const [usePost, setPost] = useState(data);
  const history = useHistory();
  const { subcategory } = usePost;

  const altUpdate = () => {
    if (Bracael.isValid[subcategory](usePost)) {

      if (!document.querySelector('[role="over"]')) {
        ReactDOM.render(
          <ComponentOverLoading />,
          document.body.appendChild(createDomModal)
        );
      };

      functions().httpsCallable('set')({
        val: `public/feed/${id}`,
        data: usePost
      })
      .then(function ({ data }) {
        const { status } = data;

        if (status) {
          PushAlert('Produto atualizado!');
          history.push('/products')
        }
        else {
          PushAlert('Erro desconhecido. Tente novamente!');
        };

        if (document.querySelector('[role="over"]')) {
          $('[role="over"]').fadeOut(300, function () { this.remove(); });
        };
      })
      .catch(function (error) {
        PushAlert('Erro ao chamar API!');

        if (document.querySelector('[role="over"]')) {
          $('[role="over"]').fadeOut(300, function () { this.remove(); });
        };
      });
    }
    else {
      PushAlert('H algo errado!');
    };
  };

  return (
    <Container>
      <Menu>
        <Name>
          <Category>{Bracael.subcategory_names[subcategory]}</Category>
          <P>Voc est publicando para {Bracael.subcategory_names[subcategory]}</P>
        </Name>
        <Buttons>
          <Go type="button" onClick={() => altUpdate()}>Atualizar</Go>
          <Simple type="button" onClick={() => history.push('/products')}>Fechar</Simple>
        </Buttons>
      </Menu>
      <Main>
        <Basic>
          <ComponentEditThumb {...{ usePost, setPost }} />
          <ComponentEditTitle {...{ usePost, setPost }} />
          <ComponentEditPrice {...{ usePost, setPost }} />
          <ComponentEditDesciption {...{ usePost, setPost }} />
          <ComponentEditInstallment {...{ usePost, setPost }} />

          {Bracael.p[8].includes(subcategory)
          ? <ComponentEditStocks {...{ usePost, setPost }} />
          : null}

          {(Bracael.p[1].includes(subcategory))
          ? <ComponentEditSizeNumber {...{ usePost, setPost }} />
          : null}

          {(Bracael.p[2].includes(subcategory))
          ? <ComponentEditSizeLetter {...{ usePost, setPost }} />
          : null}

          {Bracael.p[3].includes(subcategory)
          ? <ComponentEditCloth {...{ usePost, setPost }} />
          : null}

          {Bracael.p[4].includes(subcategory)
          ? <ComponentEditColor {...{ usePost, setPost }} />
          : null}

          {Bracael.p[5].includes(subcategory)
          ? <ComponentEditRingNumber {...{ usePost, setPost }} />
          : null}

          {Bracael.p[9].includes(subcategory)
          ? <ComponentEditSizePulseira {...{ usePost, setPost }} />
          : null}

          {Bracael.p[10].includes(subcategory)
          ? <ComponentEditSizeColar {...{ usePost, setPost }} />
          : null}

          {Bracael.p[6].includes(subcategory)
          ? <ComponentEditSizeLength {...{ usePost, setPost }} />
          : null}

          {Bracael.p[7].includes(subcategory)
          ? <ComponentEditSortClass {...{ usePost, setPost }} />
          : null}
        </Basic>
      </Main>
    </Container>
  )
}

export default ComponentEditProduct;
