import styled from "styled-components";

import { BorderRadius, LabelCheckbox } from "./root";

export const Content = styled.div`
  width: 100%;
  background: #efefef;
  padding: 1.4rem;
  flex-direction: column;
  display: flex;

  ${BorderRadius(3)}

  & > *:not(:last-child) {
    margin-bottom: 1.4rem;
  }
`

export const Title = styled.span`
  text-transform: uppercase;
  font-weight: 900;
`

export const Main = styled.div`
  flex-direction: column;
  display: flex;
`

export const Checkbox = styled.div`
  width: 100%;
  align-items: center;
  display: flex;

  &:not(:last-child) {
    margin-bottom: 1rem;
  }

  & input {
    ${LabelCheckbox}
  }

  & label {
    cursor: pointer;
  }
`
