import React, { useEffect } from 'react';
import { format } from 'date-fns';

import { Modal, Close, Content } from '../styles/Modal';
import { Title, Main, Footer } from '../styles/User';

const { jQuery: $ } = window;

function ComponentModalUser({ data, useModal, setModal }) {

  const modalClose = () => setModal(true);

  useEffect(() => {
    if (document.querySelector('[role="modal"]')) {
      $('[role="modal"]').fadeIn(200);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const { phone, receiver, date_approved, shipping_value, subtotal, address_optional, address_number, address_street, address_district, address_region, address_zone } = data[1];

  return (
    <Modal>
      <Close onClick={() => modalClose()} />
      <Content>
        <Title>
          <span>{receiver}</span>
        </Title>
        <Main>
          <div>
            <p>Endereo</p>
            <p>{address_street}, {address_number}{address_optional ? ` - ${address_optional}` : ''}<br />{address_district} - {address_region} - {address_zone}</p>
          </div>
          <div>
            <p>Telefone</p>
            <p>{phone}<br />
              <button type="button" onClick={() => window.location.href = `tel:${phone.replace(/[^0-9]+/g, '')}`}>Ligar</button> ou <button type="button" onClick={() => window.open(`https://api.whatsapp.com/send?phone=55${phone.replace(/[^0-9]+/g, '')}`)}>Whatsapp</button>
            </p>
          </div>
          <div>
            <p>TOTAL</p>
            <p>{Number(subtotal + shipping_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</p>
          </div>
        </Main>
        <Footer>
          <p>{format(new Date(date_approved), "dd/MM/yyyy 's' h:m aa")}</p>
          <button type="button" onClick={() => modalClose()}>Fechar</button>
        </Footer>
      </Content>
    </Modal>
  );
}

export default ComponentModalUser;
