import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { useHistory } from 'react-router-dom';

import { PushAlert } from '../Bracael';
import { auth } from '../utils/Connection';
import { SnapshotConnection, SnapshotOverlay } from '../Context';

import ComponentModalSimple from './ComponentModalSimple';

import { Content, Title, Commerce, Nav } from '../styles/Hidden';

function ComponentHiddenMenu() {

  const history = useHistory();

  // eslint-disable-next-line no-unused-vars
  const { useOverlay, setOverlay } = SnapshotOverlay();

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { root } = useConn;
  const { index = '' } = root ? root : {};

  const [useIndex, setIndex] = useState({})
  const { brand = '', commerce = '', invoice = '', onwer = '' } = useIndex;

  useEffect(() => {
    if(index){
      fetch(`https://bracaelcom.firebaseio.com/client/${index}.json`,{
        method: 'GET',
        mode: 'cors',
        cache: 'default'
      })
      .then(response => response.json())
      .then(result => setIndex(result))
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const useComponent = (() => {
    const Root = document.createElement('div')
    Root.setAttribute('role', 'modaly');
    return Root
  })()

  const LeaveAccount = () => {
    const data = {
      title: 'Sair da conta?',
      message: 'Confirme para sair da conta.',
      button: ['Cancelar', 'Confirmar'],
      buttonColor: '#ff2222',
      action: function () {
        setOverlay(prevState => !prevState)
        history.push('/')
        auth().signOut()
          .then(() => {
            PushAlert('Desconectado com sucesso!')
          })
      }
    }
    RenderComponent(data)
  }

  const RenderComponent = data => {
    ReactDOM.render(<ComponentModalSimple {...data} />, document.body.appendChild(useComponent))
  }

  return (
    <Content>
      <Title>
        <span>Ferramentas</span>
      </Title>
      <div>
        <Commerce>
          <li>
            <span>Razo social</span>
            <p>{brand}</p>
          </li>
          <li>
            <span>Comrcio</span>
            <p>{commerce}</p>
          </li>
          <li>
            <span>Status</span>
            <p>{invoice ? 'Ativo' : 'Pendente'}</p>
          </li>
          <li>
            <span>Em nome de</span>
            <p>{onwer}</p>
          </li>
        </Commerce>
      </div>
      <div>
        <Nav>
          <button type="button" onClick={() => alert('Pgina em desenvolvimento...')}>Pedidos arquivados</button>
          <button type="button" onClick={() => alert('Pgina em desenvolvimento...')}>Pedidos excludos</button>
        </Nav>
      </div>
      <div>
        <small>Controle as configuraes do seu comrcio e gerencie todos os pedidos recebidos at a entrega para o cliente com uma excelente experincia.</small>
      </div>
      <div>
        <button type="text" onClick={() => LeaveAccount()}>
          <svg xmlns="http://www.w3.org/2000/svg" height="16" viewBox="0 0 515.556 515.556" width="16"><path d="m225.556 0h64.444v257.778h-64.444z" /><path d="m322.222 73.944v68.602c56.798 24.932 96.667 81.559 96.667 147.454 0 88.832-72.28 161.111-161.111 161.111s-161.111-72.279-161.111-161.111c0-65.896 39.869-122.523 96.667-147.454v-68.602c-93.051 27.813-161.112 114.097-161.112 216.056 0 124.358 101.182 225.556 225.556 225.556s225.556-101.198 225.556-225.556c0-101.959-68.061-188.243-161.112-216.056z" /></svg>
          <span>Sair</span>
        </button>
      </div>
    </Content>
  );
}

export default ComponentHiddenMenu;
